import React, { useEffect, useState } from 'react'
import { Container } from './style'
import HouseCard from '../HouseCard'
import { useLocation, useNavigate } from 'react-router-dom';
import useSearch from '../../hooks/useSearch';
import Filter from '../Filter';


function Properties() {
  const [data, setdata] = useState([])
  const { search } = useLocation()
  const query = useSearch()
  const navigate = useNavigate()

  useEffect(() => {
    fetch(`https://housing-backend-production.up.railway.app/api/houses${search}`)
      .then(res => res.json())
      .then(res => {
        const categoryFilter = query.get('category')?.toLowerCase();
        if (categoryFilter) {
          const filteredData = res.filter(item => 
            item.category?.name.toLowerCase() === categoryFilter
          );
          setdata(filteredData);
        } else {
          setdata(res);
        }
      });
  }, [search, query]);

  const onSelect = (id) => {
    navigate(`/properties/${id}`)
  }

  return (
    <>
      <Filter />
      <div className="title" style={{marginTop:"50px"}}>Properties</div>
      <div className="info" style={{textAlign:"center"}}>Nulla quis curabitur velit volutpat auctor bibendum consectetur sit.</div>
      <Container>
        {data.map((value, index) => {
          return <HouseCard key={index} data={value} onClick={() => onSelect(value._id)} />
        })}
      </Container>
    </>
  )
}

export default Properties