import styled from "styled-components";

const Container = styled.div`
    /* display: flex;
    flex-wrap: wrap;
    justify-content: space-evenly;
    gap: 20px; */

    display: grid;
    grid-auto-flow: row;
    grid-template-columns: repeat(auto-fit, minmax(330px, 1fr));
    grid-gap: 20px;
    max-width: 1440px;
    width: 100%;
    margin: 0 auto;
    padding: var(--padding);
    margin-top: 90px;
`

export { Container }