import { useEffect, useRef, useState } from 'react';
import { Dropdown } from 'antd';
import { Container, Icons, MenuWrapper, Section, SelectStyled } from './style';
import { Input, Button } from '../Generic';
import { uzeReplace } from '../../hooks/uzeReplace'
import { useNavigate, useLocation } from 'react-router-dom';
import useSearch from '../../hooks/useSearch';

function Filter() {
  const [data, setData] = useState([])
  const [value, setValue] = useState('Select category')

  const handleMenuClick = (e) => {
    e.stopPropagation();
  };

  const navigate = useNavigate()
  const location = useLocation()
  const query = useSearch()


  const onChange = ({ target: { name, value } }) => {
    navigate(`${location.pathname}${uzeReplace(name, value)}`)
  }

  useEffect(() => {
    fetch(`https://housing-backend-production.up.railway.app/api/categories/list`)
      .then(res => res.json())
      .then(res => {
        setData(res || []);
      });
  }, []);

  useEffect(() => {
    let [a] = data?.filter((item) => {
      return item.name === query.get('category')
    })
    a?.name && setValue(a.name)
  }, [query, data])

  const onChangeSort = (sort) => {
    navigate(`${uzeReplace('sort', sort)}`)
  }

  const onChangeCategory = (category_id) => {
    navigate(`${uzeReplace('category', category_id)}`)
  }

  const menu = (
    <MenuWrapper onClick={handleMenuClick}>
      <h1 className="subTitle">Address</h1>
      <Section>
        <Input defaultValue={query.get('country')} onChange={onChange} name='country' placeholder='Country' />
        <Input defaultValue={query.get('region')} onChange={onChange} name='region' placeholder='Region' />
        <Input defaultValue={query.get('city')} onChange={onChange} name='address' placeholder='City' />
        <Input defaultValue={query.get('zipCode')} onChange={onChange} name='zipCode' placeholder='Zip code' />
      </Section>
      <h1 className="subTitle">Apartment info</h1>
      <Section>
        <Input placeholder='Rooms' name="room" onChange={onChange} />
        <SelectStyled value={query.get('sort') || "Select sort"} onChange={onChangeSort}>
          <SelectStyled.Option value='asc'>O'suvchi</SelectStyled.Option>
          <SelectStyled.Option value='desc'>Kamayuvchi</SelectStyled.Option>
        </SelectStyled>
        <SelectStyled value={value} onChange={onChangeCategory}>{
          data.map((value) => {
            return (
              <SelectStyled.Option key={value.id} value={value.name}>
                {value.name}
              </SelectStyled.Option>)
          })
        }
        </SelectStyled>
      </Section>
      <h1 className="subTitle">Price</h1>
      <Section>
        <Input type="number" onChange={onChange} name="min_price" placeholder='Min price' />
        <Input type="number" onChange={onChange} name="max_price" placeholder='Max price' />
      </Section>
    </MenuWrapper>
  )

  return (
    <Container>
      <Input icon={<Icons.HousesIcon />} placeholder='Enter an address, neighborhood, city, or ZIP code' />

      <div>
        <Dropdown menu={{ items: [{ key: '1', label: menu }] }} trigger={['click']} placement="bottomRight">
          <div>
            <Button className="advanced" type='light'><Icons.SettingIcon /> Advanced</Button>
          </div>
        </Dropdown>

        <Button className="search" width="180px"><Icons.SearchIcon /> Search</Button>
      </div>
    </Container>
  );
}

export default Filter;
