import styled from "styled-components";
import { ReactComponent as Houses } from '../../assets/icons/houses.svg'
import { ReactComponent as Setting } from '../../assets/icons/setting.svg'
import { ReactComponent as Search } from '../../assets/icons/search.svg'
import { Select } from "antd";


const Container = styled.div`
    display: flex;
    gap: 20px;
    padding: var(--padding);
    padding-top: 10px;
    padding-bottom: 10px;
    width: 100%;
    max-width: 1440px;
    margin: 0 auto;
    z-index: 500;
    background-color: white;

    div {
        display: flex;
        gap: 20px;

        
            @media (max-width: 900px) {
                width: 100%;
                .search, .advanced {
                    margin-top: 24px;
                    width: 100%;
                }
            }
    }

    @media (max-width: 900px) {
        width: 95%;
        display: block;
        margin-left: 2.5%;
        position: absolute;
        bottom: 62px;
    }
`

const Icons = {
    SearchIcon: styled(Search)`
     margin-right: 8px`,
    HousesIcon: styled(Houses)`
     margin-right: 8px`,
    SettingIcon: styled(Setting)`
     margin-right: 8px`
}

const MenuWrapper = styled.div`
    padding: 30px;
    background-color: white;
`

const Section = styled.div`
    display: flex;
    justify-content: flex-start;
    gap: 20px;
    margin-bottom: 20px;
`

const { Option } = Select;


const SelectStyled = styled(Select)`
    min-width: 200px;
    width: 100%;
    max-width: 200px;
    height: 44px;
`

SelectStyled.Option = styled(Option)``

export { Container, Icons, MenuWrapper, Section, SelectStyled }